body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    background-color: aliceblue;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}


.heading {
    position: relative;
    text-align: center;

    height: -webkit-fit-content;

    height: -moz-fit-content;

    height: fit-content;
    max-height: 20vh;
    overflow: hidden;

    margin-bottom: 2vh;
}
.heading img{
    width: 100%;
}
.heading #text{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    margin: 0;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
#text h1{
    text-align: center;
    margin: 0 max(0.5vw, 8px);
}
#text .symbol{
    font-size: max(2.2vw, 35px);
    font-weight: 500;
}


.customControls {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    margin-top: -15px;

    display: flex;
    justify-content: center;
}

.downloadBtn {
    font-size: x-large;
    font-weight: 500;
    color: white;

    background-color: black;

    padding: 8px;
    margin-bottom: 3px;

    border: 3px solid gray;
    border-radius: 5px;
}

@media (max-width: 500px) {

    .customControls {
        margin-top: -8px;
    }

    .downloadBtn {
        font-size: large;

        padding: 7px;
        margin-bottom: 2px;
    }
}
